import drawQrcode from '../../components/my-components/weapp.qrcode.esm.js'
import savePic from '../../pages/commodity/index/savePicToAlbum.js'
import longButton from '../../components/my-components/LongButton.vue'
export default {
    components: {
        longButton
    },
    data() {
        return {
            webviewStyles: {
                progress: {
                    color: '#FF3333'
                }
            },
            from: '',
            show: true
        }
    },
    methods: {
        sure() {
            uni.reLaunch({
                url: '/pages/index/index'
            })
        },
        longtap() {
            let that = this;
            this.closePageLoading();
            this.show = false;
            this.Dialog.confirm({
                title: '保存',
                message: '保存二维码到本地相册'
            }).then(() => {
                this.show = true;
                uni.canvasToTempFilePath({
                    x: 0,
                    y: 0,
                    width: 200,
                    height: 200,
                    destWidth: 200,
                    destHeight: 200,
                    canvasId: 'myQrcode',
                    success: function(res) {
                        savePic(that, res.tempFilePath); //保存图片至相册 
                    }
                }) // on cancel
            }).catch(() => {
                this.show = true;
            });
        }
    },
    onLoad(option) {
        this.from = decodeURIComponent(option.url);
        let ctx = uni.createCanvasContext('myQrcode');
        drawQrcode({ //二维码生成
            width: 200,
            height: 200,
            canvasId: 'myQrcode',
            ctx: ctx,
            text: this.from,
            // v1.0.0+版本支持在二维码上绘制图片
            image: {
                imageResource: '',
                dx: 70,
                dy: 70,
                dWidth: 60,
                dHeight: 60
            },
            callback() {}
        })
    }
}
